.container {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.innerContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.logo {
  transition: var(--defaultTransition);
  margin-bottom: 40px;

  &:hover,
  &:focus {
    transform: scale(1.1);
  }
}
