:root {
  --accent: #d03664;
  --darkAccent: rgba(170, 43, 81, 1);
  --hover: #d0366497;
  --black: #000000;
  --white: #ffffff;
  --gray: #585858;

  --defaultTransition: all 250ms cubic-bezier(0.47, 0, 0.745, 0.715);
  --fontFamily: "Comfortaa";
}

@font-face {
  font-family: "Comfortaa";
  font-weight: 300;
  src: local("Comfortaa-Light"),
    url("../assets/fonts/Comfortaa-Light.ttf") format("truetype");
}

@font-face {
  font-family: "Comfortaa";
  font-weight: 400;
  src: local("Comfortaa-Regular"),
    url("../assets/fonts/Comfortaa-Regular.ttf") format("truetype");
}

@font-face {
  font-family: "Comfortaa";
  font-weight: 500;
  src: local("Comfortaa-Medium"),
    url("../assets/fonts/Comfortaa-Medium.ttf") format("truetype");
}

@font-face {
  font-family: "Comfortaa";
  font-weight: 600;
  src: local("Comfortaa-SemiBold"),
    url("../assets/fonts/Comfortaa-SemiBold.ttf") format("truetype");
}

@font-face {
  font-family: "Comfortaa";
  font-weight: 700;
  src: local("Comfortaa-Bold"),
    url("../assets/fonts/Comfortaa-Bold.ttf") format("truetype");
}

body {
  margin: 0;
  font-family: "Comfortaa", cursive;
  min-height: 100vh;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  box-sizing: border-box;
  font-family: "Comfortaa", cursive;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

ul,
ol {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

input,
select {
  font: inherit;
}
